.uppercase {
  text-transform: uppercase !important;
}

body {
  margin: 0;
  padding: 0;
  font-family: "Open Sans", Helvetica, Arial, sans-serif;
  font-size: 14px;
  background-color: #262626;
}

div.app-header {
  padding: 5px 0;
  font-family: "Lato", Helvetica, Arial, sans-serif;
  width: 100%;
  min-width: 600px;
  height: 40px;
  background-color: black;
  color: white;
  border-bottom: 1px solid #4f0404;
}
div.app-header div.header-title {
  float: left;
}
div.app-header div.header-title h2.title {
  margin: 7px 15px;
  font-size: 21px;
  line-height: 25px;
  letter-spacing: 2px;
}
div.app-header div.header-title h2.title a {
  color: white;
  text-decoration: none;
}
div.app-header div.header-title h2.title a:visited {
  color: white;
}
div.app-header div.header-title h2.title a:hover {
  color: #f64242;
}
div.app-header div.header-nav {
  float: right;
  margin: 7px 15px;
}
div.app-header div.header-nav a.navigation.nav-link {
  margin: 0 10px;
  font-size: 15px;
  text-transform: uppercase;
  text-decoration: none;
  line-height: 25px;
  letter-spacing: 1px;
  font-weight: 300;
  color: white;
}
div.app-header div.header-nav a.navigation.nav-link.current {
  color: #c80a0a;
  cursor: default;
}
div.app-header div.header-nav a.navigation.nav-link:hover, div.app-header div.header-nav a.navigation.nav-link.current:hover {
  color: #f64242;
}

/* search bar */
input.search {
  display: block;
  width: 250px;
  margin: 7px auto;
  padding: 4px 6px;
  border-radius: 3px;
  border: 1px solid #333;
}

table.table {
  margin: 5px auto;
  font-size: 13px;
  border-collapse: collapse;
  border: 1px solid #333;
}

/* table header */
thead.table-header {
  font-weight: bold;
  cursor: default;
}
thead.table-header th.table-header {
  padding: 8px 4px;
  border: 1px solid #333 !important;
  color: black;
  background-color: #707070;
  text-transform: capitalize;
}
thead.table-header th.table-header.sortable {
  cursor: pointer;
}
thead.table-header th.table-header.sortable:hover {
  background-color: #5e5e5e;
}

/* table body */
tbody.table-body tr.odd-row {
  background-color: #f2f2f2;
}
tbody.table-body tr.even-row {
  background-color: #d9d9d9;
}
tbody.table-body tr:hover {
  cursor: default;
  background-color: #fcbcbc;
}
tbody.table-body tr td.table-cell {
  margin: 0;
  padding: 8px 4px;
  border-left: 1px solid #525252;
  border-right: 1px solid #525252;
}
tbody.table-body tr td.table-cell.left-end {
  border-left-color: #333;
}
tbody.table-body tr td.table-cell.right-end {
  border-right-color: #333;
}

@media screen and (min-width: 1001px) {
  table.table {
    width: 1000px;
  }

  div.app-header h2.title {
    font-weight: 100;
  }
}
@media screen and (max-width: 1000px) {
  table.table {
    width: 600px;
  }

  div.app-header h2.title {
    font-weight: 300;
    display: block;
    background-image: url("../persona5.png");
  }

  .mobile-hidden {
    display: none !important;
  }
}



